import { Component, ReactNode } from "react";
import PropTypes from "prop-types";
import * as Sentry from "@sentry/react";

interface Error {
  stack?: string;
}

interface ErrorInfo {
  componentStack?: string;
}

export default class ErrorBoundary extends Component {
  state = {
    error: "",
    errorInfo: {
      componentStack: {},
    },
    hasError: false,
  };
  static getDerivedStateFromError(error: Error) {
    // receives the error and updates the state variables, error and hasError.
    return {
      hasError: true,
      error,
    };
  }
  static propTypes: { children: PropTypes.Validator<object> };

  componentDidCatch(error: Error, errorInfo: ErrorInfo) {
    //receives the error, which represents the error that was thrown and errorInfo which
    //  is an object with a componentStack key containing information about which component
    //threw the error.Here we logged the error and also update the state with the errorInfo.
    // It’s totally up to you what you want to do with these two.
    console.log({ error, errorInfo });
    this.setState({
      error,
      errorInfo,
    });
    console.log(Sentry);

    Sentry.captureEvent(
      {
        event_id: "error aquiii siii",
      },
      {
        data: {
          error: "error aqui noo",
        },
      }
    );
  }

  render(): ReactNode {
    const { hasError, errorInfo } = this.state;
    if (hasError) {
      return (
        <div className="card my-5">
          <div className="card-header">
            <p>
              There was an error in loading this page.{" "}
              <span
                style={{ cursor: "pointer", color: "#0077FF" }}
                onClick={() => {
                  window.location.reload();
                }}
              >
                Reload this page
              </span>{" "}
            </p>
          </div>
          <div className="card-body">
            <details className="error-details">
              <summary>Click for error details</summary>
              {errorInfo && errorInfo.componentStack.toString()}
            </details>
          </div>
        </div>
      );
    }

    return this.props.children;
  }
}

ErrorBoundary.propTypes = {
  children: PropTypes.oneOfType([PropTypes.object, PropTypes.array]).isRequired,
};

export function findMissingLetter(array: string[]): string {
  const newArr = array.map((letter) => letter.charCodeAt(0));

  const getSum = (arr: number[]) =>
    arr.reduce(
      (previousValue, currentValue) => previousValue + currentValue,
      0
    );

  const sumThisArr = getSum(newArr);

  let completedArr = 0;

  for (let i = newArr[0]; i <= newArr[newArr.length - 1]; i++) {
    completedArr += i;
  }

  return String.fromCharCode(completedArr - sumThisArr);
}
